





























import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component
export default class Details extends Vue{
  dayTotal(value:recordItem[]) {
    let ans = 0;
    for(let i = 0; i <  value.length; i++) {
      ans += parseFloat(value[i].amount.toString());
    }
    return ans;
  }
  get recordList() {
    const map: {[key: string]: recordItem[]} = {};

    this.$store.commit('fetchOneMonthRecord');
    // console.log(123, this.$store.state.record);
    for(const item of this.$store.state.oneMonthRecord) {
      const date = item.times.split(' ')[0];
      map[date] = map[date] || [];
      map[date].push(item);
    }
    const newMap = {};
    Object.keys(map).sort().reverse().forEach(function(key) {
      newMap[key] = map[key];
    });
    return newMap;
  }

  created() {
    this.$store.commit('fetchRecords')
  }
}
